import ListGroupItem from 'common-lib/components/bootstrap/ListGroupItem';
import Image from 'common-lib/components/Image';
import Col from 'common-lib/components/bootstrap/Col';
import Row from 'common-lib/components/bootstrap/Row';
import { connect } from 'inferno-mobx'
import Component from 'common-lib/components/Component';

@connect(['boats'])
export default class BoatListItem extends Component {
    componentWillMount() {
        process.env.BROWSER && this.props.boats.fetchPrimaryImage(this.props.boat);
    }

    render() {
        const {boat, boats: boatsStore} = this.props;

        return (
            <ListGroupItem onClick={() => this.context.router.push(`/boat/${boat.url}`)}>
                <Row>
                    <Col xs={4} style={{lineHeight: '59px', height: 75}}>{<Image src={boat.primaryImage.src} style={{maxHeight: 75, maxWidth: '100%', verticalAlign: 'middle'}}/>}</Col>
                    <Col xs={8}>
                        <h4>{boat.name}</h4>
                        <div>
                            {boat.length} {boat.lengthUnit === 'feet' ? 'foot' : 'metre'} {boat.builder} {boat.model}
                        </div>
                    </Col>
                </Row>
            </ListGroupItem>
        )
    }
}






