import {Link} from 'inferno-router';
import Col from 'bs/Col';

export default () => (
    <div>
        <Col id="search-buttons" xs={12} sm={6} smOffset={3} md={4} mdOffset={4}>
            <Link className="btn btn-primary btn-block" to="/boats/search/by-boat-name">Search by boat name</Link>
        </Col>
    </div>
);




