export default ({header, children}) => (
    <div style={styles.box}>
        <div style={styles.boxHeader}>{header}</div>
        <div style={styles.boxBody}>
            {children}
        </div>
    </div>
)

const styles = {
    box: {
        borderRadius: 15,
        background: '#a3e63e',
        color: '#333',
        paddingBottom: 5
    },
    boxHeader: {
        color: 'white',
        textAlign: 'center',
        fontSize: 20,
        backgroundColor: '#83c61e',
        borderTopLeftRadius: 15,
        borderTopRightRadius: 15,
        paddingTop: 5,
        paddingBottom: 5
    },
    boxBody: {
        padding: 5
    }
}


