import Inferno from 'inferno'
import { Route } from 'inferno-router'
import Layout from '../components/layout/Layout'
import NotFound from '../components/layout/404'
import Todos from '../pages/Todos'
import About from '../pages/About'
import BoatsSearch from '../pages/BoatsSearch'
import BoatsSearchByBoatName from '../pages/BoatsSearchByBoatName'
import BoatsListByBoatName from '../pages/BoatsListByBoatName'
import BoatPage from '../pages/BoatPage';

import Login from '../components/account/Login'
import Logout from '../components/account/Logout'
import Register from '../components/account/Register'
import HomePage from '../pages/home/HomePage';
import ContactUs from '../pages/ContactUs';
import ContactOwner from '../pages/ContactOwner';

/**
 * Routes are defined here.
 * @param {object} - stores
 * @returns {object}
 */
export default function({ account }) {

    function isAuthenticated(nextState, router) {
        if (!account.isLoggedIn()) {
            //router.push('/page/login')
        }
    }

    return (
        <Route component={ Layout }>
            <Route path="/" component = {HomePage} />
            <Route path="/boats/search" component={BoatsSearch} />
            <Route path="/boats/search/by-boat-name" component={BoatsSearchByBoatName} />
            <Route path="/boats/list/by-boat-name/:name" component={BoatsListByBoatName} />
            <Route path="/boat/:boatUrl" component={BoatPage} />
            <Route path="/contact/us" component={ContactUs} />
            <Route path="/contact/owner/:ownerId" component={ContactOwner} />
            <Route path="/page/todos" component={ Todos } onEnter={ isAuthenticated }/>
            <Route path="/page/about" component={ About }/>
            <Route path="/page/login" component={ Login }/>
            <Route path="/page/logout" component={ Logout }/>
            <Route path="/page/register" component={ Register }/>
            <Route path="*" component={ NotFound }/>
        </Route>
    )
}



