import Btn from 'common-lib/components/form/Btn';
import Form from 'common-lib/components/form/Form';
import TextField from 'common-lib/components/form/TextField';
import Col from 'bs/Col';
import {connect} from 'inferno-mobx'
import Component from 'common-lib/components/Component'

@connect([])
export default class BoatsSearchByBoatName extends Component {
    doSearch(values) {
        values.name && this.context.router.push(`/boats/list/by-boat-name/${values.name}`);
    }

    render() {
        return (
            <div>
                <Col xs={12} sm={8} smOffset={2} md={6} mdOffset={3}>
                    <Form onSubmit={this.doSearch.bind(this)}>
                        <TextField name="name" placeholder="Enter boat name" autoFocus/>
                        <Col style={{paddingTop: 10}} xs={10} xsOffset={1} sm={6} smOffset={3}>
                            <Btn type="submit" block onClick={this.doSearch.bind(this)}>Search</Btn>
                        </Col>
                    </Form>
                </Col>
            </div>
        )
    }
}










