import { extendObservable, observable } from 'mobx'

export default class BoatsSearchList {

    constructor(request, state = {}) {
        this.request = request
        extendObservable(this, {
            loading: false,
            boatIds: observable.array()
        }, state)
    }

    clear() {
        this.boatIds = observable.array();
    }

    doSearch(name, boatsStore) {
        this.clear();
        this.loading = true;
        return this.request(`api/boats/search/by-boat-name/${encodeURIComponent(name)}`)
            .then(boats => {
                this.loading = false;
                boats.map(boat => boatsStore.addOrUpdate(boat))
                this.boatIds = this.boatIds.concat(boats.map(b => b._id));
            })
    }
}



