import Icon from './Icon';

export default ({href, icon, block, style, children}) => {
    return (
        <a href={href} style={{display: block ? 'block' : 'inline-block', whiteSpace: 'nowrap', ...style}}>
            {icon ? <Icon name={icon} style={{paddingRight: 10}} /> : null}
            {children}
        </a>
    )
}


