import cn from 'classnames';

export default ({title, children, className, ...props}) => {
    const cls = cn('panel panel-default', className);

    return (
    <div class={cls} {...props}>
        {title ? <div class="panel-heading">{title}</div> : null}
        <div class="panel-body">
            {children}
        </div>
    </div>
)}


