import Section from 'common-lib/components/Section';
import {Link} from 'inferno-router';

const BoatDetails =  ({boat}) => (
    <div style={{backgroundColor: '#f6f6f6'}}>
        <Section title="details">
            <div className="details-desc">
                <HandListItem label="Length" text={`${boat.length || ''} ${boat.lengthUnit || 'feet'}`}/>
                <HandListItem label="Year" text={boat.year}/>
                <HandListItem label="Build" text={boat.builder}/>
                <HandListItem label="Model" text={boat.model}/>
                <HandListItem label="Description" text={boat.description}/>
            </div>

            <div>
                <Link to={`/contact/owner/${boat._id}`}>
                    <span style={{
                    width: '100%',
                    backgroundColor: '#2d3e50',
                    border: '0px',
                    color: 'white',
                    height: 40,
                    fontSize: 10,
                    lineHeight: '40px',
                    display: 'inline-block',
                    borderRadius: 25,
                    textAlign: 'center',
                    fontWeight: 700,
                    textTransform: 'uppercase',
                }}>

                    Contact the Owner
                    </span>
                </Link>
            </div>
        </Section>
    </div>
);

const HandListItem = ({label, text}) => (
    <div style={{
        fontSize: 13,
        lineHeight: '15px',
        fontWeight: 400,
        textTransform: 'uppercase',
        letterSpacing: '0.5px',
        marginBottom: 20
    }}>
        <Bullet name="hand-o-right" style={{paddingRight: 5}}/>
        <label style={{color: '#222', fontWeight: 'bold', paddingRight: 5}}>{label}:</label>
        <span style={{color: 'black'}}>{text}</span>
    </div>
);

export default BoatDetails;

const Bullet = () => (<span style={{paddingRight: 10, fontSize: 16}}>&bull;</span>)



