import ListGroup from 'common-lib/components/bootstrap/ListGroup';
import BoatListItem from './BoatListItem';
import PageSpinner from 'common-lib/components/PageSpinner';
import Col from 'common-lib/components/bootstrap/Col';

export default (({boats, loading}) => {
    if(loading) {
        return (
            <PageSpinner text="Finding boats"/>
        )
    }

    if(boats.length) {
        return (
            <Col xs={12} sm={8} smOffset={2} md={8} mdOffset={2}>
                <ListGroup>
                    {boats.map(boat => <BoatListItem key={boat._id} boat={boat}/>)}
                </ListGroup>
            </Col>
        )
    }

    return (
        <h4>No boats found</h4>
    )
});




