import Inferno from 'inferno'
import Component from 'inferno-component'
import Header from './Header'

export default class Layout extends Component {
    render({ children }) {
        return <div>
            <Header/>
            <div style={styles.content}>
                {children}
            </div>
        </div>
    }
}

const styles = {
    content: {
        marginTop: 85
    }
};



