import {extendObservable, observable} from 'mobx'

export default class BoatsStore {

    constructor(request, state = {}) {
        state.boats && state.boats.map(boat => this._ensureBoatDefaults(boat));
        this.request = request
        extendObservable(this, {
            loading: false,
            boats: observable.array([]),
            getBoatByUrl: url => this.boats.find(b => b.url === url),
        }, state)

    }

    _ensureBoatDefaults(boat) {
        boat.images = boat.images || [];
        boat.primaryImage = boat.primaryImage || {};
        boat.trips = boat.trips || [];
        boat.crew = boat.crew || [];
    }

    contactOwner(values) {
        return this.request(`api/contact/owner`, values);
    }

    fetchBoatCrew(boat) {
        return this.request(`api/boat/${boat._id}/crew`).then(crew => {
            crew.forEach(member => member.images = member.images || []);
            boat.crew = crew;
            return boat.crew;
        });
    }

    fetchBoatTrips(boat) {
        return this.request(`api/boat/${boat._id}/trips`).then(trips => {
            trips.forEach(trip => trip.images = trip.images || []);
            boat.trips = trips;
            return boat.trips;
        })
    }

    fetchMemberImages(member) {
        member.userId ? (
                this.request(`api/user/${member.userId}/images`).then(images => member.images = images)
            ) : (
                this.request(`api/crew/${member._id}/images`).then(images => member.images = images)
            );
    }

    fetchTripImages(trip) {
        this.request(`api/trip/${trip._id}/images`).then(images => trip.images = images)
    }

    fetchBoatImages(boat) {
        this.request(`api/boat/${boat._id}/images`).then(images => boat.images = images);
    }

    addOrUpdate(boat) {
        this._ensureBoatDefaults(boat);
        const found = this.boats.find(b => b._id === boat._id);
        boat = found ? extendObservable(found, boat) : this.boats.push(boat);
        return boat;
    }

    findById(id) {
        return this.boats.find(b => b._id === id);
    }

    fetchPrimaryImage(boat) {
        boat.primaryImage.src || this.request(`api/boat/primary-image/${boat._id}`).then(image => {
            boat.primaryImage = image;
        });
    }

    fetchBoatByUrl(url) {
        return new Promise((resolve, reject) => {
            const boat = this.boats.find(b => b.url === url);
            boat ? resolve(boat) : this.request(`api/boat/by-url/${url}`).then(boat => {
                    this.addOrUpdate(boat);
                    resolve(boat);
                })
        });
    }
}



