import PureRenderComponent from 'common-lib/components/PureRenderComponent';
import Panel from 'common-lib/components/bootstrap/Panel';
import cn from 'classnames';

let count = 0;
export default class CollapsiblePanel extends PureRenderComponent {

    constructor() {
        super();
        this.id = `collapsible-panel-${count++}`;
    }

    togglePanel() {
        this.setState({open: !this.state.open});
    }

    render() {
        const {title, children, ...props} = this.props;
        const {open} = this.state;
        const panelCls = cn('collapsible collapse');

        return (
            <div>
                <div style={styles.titleBar} onClick={this.togglePanel.bind(this)} data-toggle="collapse" data-target={`#${this.id}`}>
                    <button style={styles.button}>
                        {open ? '-' : '+'}
                    </button>
                    <div style={styles.title}>{title}</div>
                </div>
                <Panel id={this.id} style={styles.panel} className={panelCls}>{children}</Panel>
            </div>
        )
    }
}

const styles = {
    titleBar: {
        height: 50,
        backgroundColor: '#f6f6f6',
        textTransform: 'uppercase'
    },
    button: {
        backgroundColor: '#2d3e50',
        color: 'white',
        height: 50,
        width: 50,
        float: 'left',
        fontSize: 37
    },
    icon: {
        fontSize: 16,
        lineHeight: '30px'
    },
    title: {
        marginLeft: 70,
        fontSize: 14,
        lineHeight: '50px',
        fontWeight: 700,
        letterSpacing: '0.8px',
        cursor: 'pointer'
    },
    panel: {
        border: 'none'
    }
}


