import Component from 'common-lib/components/Component';

export default class Form extends Component {

    constructor() {
        super();
        this.fields = [];
    }

    getChildContext() {
        return {
            form: this
        }
    }

    validateForm() {
        let valid = true;
        this.fields.forEach(field => {
            field.validate() || (valid = false);
        });
        return valid;
    }

    getFormValues() {
        return this.fields.reduce((memo, field) => {
            memo[field.props.name] = field.getValue();
            return memo;
        }, {});
    }

    onSubmit(ev) {
        ev.preventDefault();
        this.validateForm() && this.props.onSubmit && this.props.onSubmit(this.getFormValues());
    }

    addField(field) {
        this.fields.push(field);
    }

    render() {
        const {children, onSubmit, ...props} = this.props;
        return (
            <form  onSubmit={this.onSubmit.bind(this)} {...props}>
                {children}
            </form>
        )
    }
}


