import Component from 'common-lib/components/Component';
import FormGroup from 'common-lib/components/form/FormGroup';

export default class FormField extends Component {

    componentWillMount() {
        this.context.form.addField(this);
    }

    validate() {
        return [...FormField.validators.keys()]
            .map(vName => this.props[vName] ? FormField.validators.get(vName)(this) : true)
            .every(valid => valid === true);
    }

    cleanProps({...props}) {
        Object.keys(props).forEach(k => FormField.validators.has(k) && (delete props[k]));
        return props;
    }

    setError(text) {
        this.setState({error: text});
    }

    clearError() {
        this.setState({error: undefined});
    }

    render(children) {
        return (
            <FormGroup controlId={this.props.name} hasError={!!this.state.error}>
                <label class="control-label">{this.props.label}</label>
                {children}
                {this.state.error && <span className="help-block">{this.state.error}</span>}
            </FormGroup>
        )
    }

    static validators = new Map();
}

createValidator('required', field => field.getValue() ? undefined : `${field.props.name} is required`);
createValidator('isEmail', field => /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(field.getValue()) ? undefined : `${field.props.name} must be a valid email`);

function createValidator(vName, validator) {
    FormField.validators.set(vName, field => {
        const errorString = validator(field);
        errorString ? field.setError(typeof field.props[vName] === 'string' ? field.props[vName] : errorString) : field.clearError();
        return !errorString;
    });
}





