import HeaderMenu from './HeaderMenu';
import Grid from '../../common-lib/components/bootstrap/Grid';
import Row from '../../common-lib/components/bootstrap/Row';

export default () => (
    <header style={styles.header}>
        <Grid>
            <Row>
                <HeaderMenu />
            </Row>
            <Row>
                <div style={{height: 1, backgroundColor: 'rgb(148, 195, 231)'}}/>
            </Row>
        </Grid>
    </header>
)

const styles  = {
    header: {
        position: 'fixed',
        left: 0,
        top: 0,
        zIndex: 100,
        backgroundColor: 'white',
        padding: 5,
        width: '100%'
    }
};





