import CollapsiblePanel from 'common-lib/components/CollapsiblePanel';
import H5 from 'common-lib/components/H5';
import Gallery from 'common-lib/components/Gallery';
import Component from 'common-lib/components/Component';
import {connect} from 'inferno-mobx';

@connect(['boats'])
export default class BoatCrew extends Component {

    componentWillMount() {
        this.props.boat.crew.length || this.props.boats.fetchBoatCrew(this.props.boat).then(crew => {
            crew.forEach(member => {
                this.props.boats.fetchMemberImages(member);
            });
        });
    }

    render() {
        const {boat} = this.props;
        return (
            <div>
                {boat.crew.map(member => <CrewMemberPanel member={member} images={member.images} key={member._id}/>)}
            </div>
        )
    }
}

const CrewMemberPanel = ({member, images=[]}) => (
    <CollapsiblePanel title={`${member.name} (${member.role})`.toUpperCase()}>
        <H5 icon="user">{member.description}</H5>
        <Gallery images={images}  />
    </CollapsiblePanel>
);




