import Row from 'common-lib/components/bootstrap/Row';
import Col from 'common-lib/components/bootstrap/Col';

const BoatHeader = ({boat = {}}) => (
        <Row id="boat-header">
            <Col xs={12} sm={8}>
                <h2 style={{margin: 0}} className="detail-title">{boat.name}&nbsp;</h2>
            </Col>
        </Row>
);

export default BoatHeader


