import Gallery from 'common-lib/components/Gallery';
import CollapsiblePanel from 'common-lib/components/CollapsiblePanel';
import Component from 'common-lib/components/Component';
import {connect} from 'inferno-mobx';

@connect(['boats'])
export default class BoatImages extends Component {

    componentWillMount() {
        process.env.BROWSER && this.props.boats.fetchBoatImages(this.props.boat);
    }

    render() {
        const {boat} = this.props;
        const images = boat.images && boat.primaryImage ? boat.images.filter(img => img._id !== boat.primaryImage._id) : boat.images || [];
        return (
            <CollapsiblePanel title="More boat images">
                <Gallery images={images} />
            </CollapsiblePanel>
        )
    }
}



