const sizes = ['xs', 'sm', 'md', 'lg'];

export default (props) => {
    let cls = sizes
        .filter(size => props[size])
        .map(size => `col-${size}-${props[size]}`);

    cls = cls.concat(sizes
        .filter(size => props[`${size}Offset`])
        .map(size => `col-${size}-offset-${props[`${size}Offset`]}`)
    );

    const {children, xs, sm, md, lg, xsOffset, smOffset, mdOffset, lgOffset, className, ...passthrough} = props;



    return (
        <div className={`col ${cls.join(' ')} ${className ? className : ''}`} {...passthrough}>{children}</div>
    )
}


