import { connect } from 'inferno-mobx'
import Component from 'common-lib/components/Component'
import Grid from 'common-lib/components/bootstrap/Grid';
import Row from 'common-lib/components/bootstrap/Row';
import Col from 'common-lib/components/bootstrap/Col';
import Section from 'common-lib/components/Section';
import BoatHeader from 'components/BoatHeader';
import BoatDetails from 'components/BoatDetails';
import BoatPrimaryImage from 'components/BoatPrimaryImage';
import SocialBtns from 'common-lib/components/SocialBtns';
import BoatImages from 'components/BoatImages';
import BoatCrew from 'components/BoatCrew';
import BoatTrips from 'components/BoatTrips';
import SailorsEscapeBlock from 'components/SailorsEscapeBlock';
import Spinner from 'common-lib/components/Spinner';

@connect(['boats'])
export default class BoatPage extends Component {

    static onEnter({ boats, common, params }) {
        return boats.fetchBoatByUrl(params.boatUrl).then(boat => {
            common.title = boat.name;
            return boat;
        });
    }

    render() {
        const {params, boats} = this.props;
        const boat = boats.getBoatByUrl(params.boatUrl);

        if(boat === undefined || boat._id === undefined) {
            return <Spinner />
        }

        return (
            <Grid>
                <Row>
                    <Col xs={12} sm={8}>
                        <Section>
                            <BoatHeader boat={boat} />
                        </Section>

                        <Section>
                            <BoatPrimaryImage boat={boat} />
                        </Section>

                    </Col>
                    <Col xs={12} sm={4}>

                        <Section>
                            <BoatDetails boat={boat}/>
                        </Section>
                        <Section>
                            <SocialBtns />
                        </Section>
                    </Col>
                </Row>
                <Row>
                    <Col xs={12} sm={8}>
                        <Section>
                            <BoatImages boat={boat} />
                        </Section>

                        <Section title="Crew">
                            <BoatCrew boat={boat} />
                        </Section>
                        <Section title="Trips">
                            <BoatTrips boat={boat}/>
                        </Section>
                    </Col>
                    <Col xs={12} sm={4}>
                        <Section>
                            <SailorsEscapeBlock />
                        </Section>
                    </Col>
                </Row>
            </Grid>
        )
    }
}



