import Form from 'common-lib/components/form/Form';
import TextField from 'common-lib/components/form/TextField';
import Textarea from 'common-lib/components/form/Textarea';
import Btn from 'common-lib/components/form/Btn';
import ButtonFormGroup from 'common-lib/components/form/ButtonFormGroup';
import PageSpinner from 'common-lib/components/PageSpinner';
import Component from 'common-lib/components/Component';
import {connect} from 'inferno-mobx';

@connect(['boats'])
export default class ContactOwnerForm extends Component {

    onSubmit(values) {
        const boatId = window.location.href.split('/').pop();
        this.setState({sending: true});
        this.props.boats.contactOwner({boatId: boatId, ...values}).then(() => {
            this.setState({sending: false});
            window.history ? window.history.back(): this.setState({success:true});
        });
    }

    render() {
        return (
            <PageSpinner text={this.state.sending ? 'Sending Message...' : undefined}>
                <Form onSubmit={this.onSubmit.bind(this)} >
                    <TextField name="name" label="Your name" required />
                    <TextField name="email" label="Your email" isEmail />
                    <Textarea name="message" label="Message" rows={6} required />
                    <ButtonFormGroup>
                        <Btn block type="submit">Send</Btn>
                    </ButtonFormGroup>
                </Form>
            </PageSpinner>
        )
    }
}






