export default () => (
    <div>
        <nav className="navbar navbar-default" style={styles.navbar}>
        <div className="container-fluid">
            <div className="navbar-header">
                <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
                    <span className="sr-only">Toggle navigation</span>
                    <span className="icon-bar"></span>
                    <span className="icon-bar"></span>
                    <span className="icon-bar"></span>
                </button>
                <a href="/">
                    <img src='/images/logo.png' width="250" alt=""/>
                </a>
            </div>

            <div className="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
                <ul className="nav navbar-nav navbar-right">
                    <Item text="Home" href="/"/>
                    <Item text="Search" href="/boats/search"/>
                    <Item text="Contact Us" href="/contact/us"/>
                </ul>
            </div>
        </div>
    </nav>
    </div>
)

const Item = ({text, href}) => (
    <li>
        <a href={href} style={styles.navitem}>
            {text}
    </a>
    </li>
);

const styles = {

    navbar: {
        backgroundColor: 'white',
        border: 'none',
        marginBottom: 0,
        paddingTop: 10,
        paddingBottom: 10
    },
    navitem: {
        color: '#222',
        fontWeight: 400,
        textTransform: 'uppercase'
    }
};


