import Image from 'common-lib/components/Image';
import Component from 'common-lib/components/Component';
import { connect } from 'inferno-mobx'

@connect(['boats'])
export default class BoatPrimaryImage extends Component {
    componentWillMount() {
        process.env.BROWSER && this.props.boats.fetchPrimaryImage(this.props.boat);
    }

    render() {
        const {boat} = this.props;
        return (
            <Image style={{width: '100%', maxWidth: 500}} src={boat.primaryImage ? boat.primaryImage.src : undefined} />
        )
    }
}



