export default ({text, children}) => (
    <div>
        <div style={{display: text ? 'block' : 'none', lineHeight: '100%', position: 'fixed', top: 0, left: 0, width: '100%', height: '100%', opacity: .5, backgroundColor: 'black', zIndex: 1}}>
            <h1 style={{position: 'relative', color: 'white', textAlign: 'center', top: '45%'}}>{text}</h1>
        </div>
        {children}
    </div>
)




