import ContactOwnerForm from './ContactOwnerForm';
import Grid from 'common-lib/components/bootstrap/Grid';
import Row from 'common-lib/components/bootstrap/Row';
import Col from 'common-lib/components/bootstrap/Col';

export default () => (
    <Grid>
        <Row>
            <Col xs={12} sm={8} smOffset={2}>
                <p>
                    Please fill out the form below with your information and it will be forwarded to the owner.
                </p>
                <ContactOwnerForm />
            </Col>
        </Row>
    </Grid>
);




