import Frame from './Frame';
import Col from 'common-lib/components/bootstrap/Col';
import Row from 'common-lib/components/bootstrap/Row'
import Grid from 'common-lib/components/bootstrap/Grid'
import {Link} from 'inferno-router';

export default ({pathname}) => (
    <div style={{paddingTop: 10}}>
        <Row>
            <Col xs={8} xsOffset={2} sm={8} smOffset={2} md={6} mdOffset={3}>
                <Frame header="Boat website creator">
                    <p>
                        Makes creating a great looking website for your boat easy. Share your trips
                        with existing friends or find new ones using social media tools.
                    </p>
                    <Grid fluid>
                    <Row>
                        <Col xs={12} sm={6} style={{paddingBottom: 20}}>
                            <a className="btn btn-default btn-block" href='/manage'>Create or update</a>
                        </Col>
                        <Col xs={12} sm={6}>
                            <Link className="btn btn-default btn-block pull-right" to="/boats/search">Find people or boats</Link>
                        </Col>
                    </Row>
                    </Grid>
                </Frame>
            </Col>
        </Row>
    </div>

);





