import Component from 'common-lib/components/Component'
import Image from 'common-lib/components/Image';
import chunk from 'lodash/chunk';
import Grid from 'common-lib/components/bootstrap/Grid';
import Row from 'common-lib/components/bootstrap/Row';
import Col from 'common-lib/components/bootstrap/Col';

let counter = 0;

export default class Gallery extends Component {
    constructor() {
        super();
        this.imageGroup = counter++;
    }

    render() {
        const {images} = this.props;
        return (
            <div>
                <Grid fluid>
                    {chunk(images, 3).map(chnk => <AnRow key={chnk[0]._id} images={chnk} group={this.imageGroup} />)}
                </Grid>
            </div>
        )
    }
}

const AnRow = ({images, group}) => (
    <Row>
        {images.map((image) => <AnImage image={image} key={image._id} group={group} />)}
    </Row>
);

const AnImage = ({image, group}) => (
    <Col xs={6} sm={4}>
        <a href={image.src} data-lightbox={group} data-title={image.description} >
            <Image src={image.src} width="100%" />
        </a>
    </Col>
);



