import Grid from 'common-lib/components/bootstrap/Grid';
import Row from 'common-lib/components/bootstrap/Row';
import Col from 'common-lib/components/bootstrap/Col';

export default () => (

    <Grid fluid style={{backgroundColor: '#f0f0fe', padding: 10}}>
        <Row>
            <Col xs={12}>
                <label>Share this page with your friends</label>
            </Col>
        </Row>
        <Row>
            <Col xs={3}>

                <div id="fb-root"></div>
                <a href={`javascript:window.open('https://www.facebook.com/sharer/sharer.php?u=' + encodeURIComponent(document.URL) + '&t=' + encodeURIComponent(document.URL))`}
                   title="Share on Facebook"
                   target="_blank">
                    <img alt="Share on Facebook" src="/images/facebook.png"/>
                </a>
            </Col>
            <Col xs={3}>
                <a href={`javascript:window.open('https://twitter.com/intent/tweet?text=check out this boat site:%20'  + encodeURIComponent(document.URL))`}
                   target="_blank"
                   title="Tweet">
                    <img alt="Tweet" src="/images/twitter.png"/>
                </a>
            </Col>
            <Col xs={3}>
                <a href={`javascript:window.open('https://plus.google.com/share?url=' + encodeURIComponent(document.URL))`}
                   target="_blank"
                   title="Share on Google+">
                    <img alt="Share on Google+" src="/images/google+.png" />
                </a>
            </Col>
            <Col xs={3}>
                <a href={`javascript:window.open('mailto:?subject=Check%20out%20this%20boat%20page&body=' + encodeURIComponent(document.URL))`}
                   title="Send email">
                    <img alt="Send email" src="/images/email.png" />
                </a>
            </Col>
        </Row>
    </Grid>
)


