import FormField from './FormField';

export default class TextField extends FormField {
    componentDidMount() {
        this.props.autoFocus && this.input.focus();
    }

    getValue() {
        return this.input.value;
    }

    render() {
         const {...props} = this.cleanProps(this.props);

        return (
            super.render(<textarea className="form-control" ref={c => this.input = c} {...props} />)
        )
    }

}



