import Component from 'common-lib/components/Component';
import { connect } from 'inferno-mobx'
import BoatList from 'components/BoatList'

@connect(['boatsSearchList', 'boats'])
export default class BoatsListByBoatName extends Component {

    static onEnter({ boatsSearchList, boats: boatsStore, common, params }) {
        common.title = 'boats by name';
        return boatsSearchList.doSearch(params.name, boatsStore);
    }

    render() {
        const {boatsSearchList} = this.props;
        const boats = boatsSearchList.boatIds.map(id => this.props.boats.findById(id));
        return <div>
            <BoatList boats={boats} loading={boatsSearchList.loading} />
        </div>;
    }
}


