import Link from 'common-lib/components/Link';
import Section from 'common-lib/components/Section';

export default () => (
    <div style={{backgroundColor: '#f8f8f8', color: '#222'}}>
        <Section title="Create your own boat website">
            <p style={{
                color: '#b9b9b9',
                fontSize: 14,
                lineHeight: '22px',
                fontWeight: 400,
                marginBottom: 25
            }}>Share your adventures with your own boat website.</p>
            <Link href="https://sailorsescape.com" icon="globe" block style={{marginBottom: 20}}>https://sailorsescape.com</Link>
            <label>Contact Us:</label>
            <Link style={{marginBottom: 10}} href="mailto:info@sailorsescape.com" icon="envelope" block>info@sailorsescape.com</Link>
            <Link href="mailto:support@sailorsescape.com" icon="envelope" block>support@sailorsescape.com</Link>
        </Section>
    </div>
)


