import CollapsiblePanel from 'common-lib/components/CollapsiblePanel';
import Gallery from 'common-lib/components/Gallery';
import {formatDate} from 'common-lib/services/DateService';
import Component from 'common-lib/components/Component';
import {connect} from 'inferno-mobx';

@connect(['boats'])
export default class BoatTrips extends Component {

    componentWillMount() {
        this.props.boat.trips.length || this.props.boats.fetchBoatTrips(this.props.boat).then(trips => {
            trips.forEach(trip => {
                this.props.boats.fetchTripImages(trip);
            })
        })
    }

    render() {
        const {boat} = this.props;
        return (
            <div>
                {boat.trips.map(trip => <TripPanel trip={trip} images={trip.images} key={trip._id} />)}
            </div>
        )
    }
}


const TripPanel = ({trip, images=[]}) => (
    <CollapsiblePanel title={`${trip.destinationName} (${formatDate(new Date(trip.startDate))})`}>
        <div style={{paddingBottom: 10}}>
            <label>Mooring type:</label><span>{trip.mooringType}</span>
        </div>
        <Gallery images={images} />
    </CollapsiblePanel>
);




