export default ({children, title}) => (
    <div style={styles.section}>
        {title ? <div style={styles.title}>{title}</div> : null}
        <div>
            {children}
        </div>
    </div>
);

const styles = {
    section: {
        marginBottom: 10,
        padding: 10
    },
    title: {
        fontSize: 22,
        lineHeight: '25px',
        fontWeight: 700,
        color: '#222',
        textTransform: 'uppercase',
        letterSpacing: 1.8,
        marginBottom: 11,
        fontFamily: '"Cabin", sans-serif',
    }
};


