import Component from 'common-lib/components/Component';
import cn from 'classnames';
import FG from 'common-lib/components/bootstrap/FormGroup';

export default class FormGroup extends Component {
    render({children, hasError}) {
        const cls = cn({'has-error': hasError});
        return (
            <FG className={cls}>
                {children}
            </FG>
        )
    }
}


